import React from 'react';
import PropTypes from 'prop-types';
import cx from 'classnames';
import anime from 'animejs';

import { Text } from '../Text';
import { Button } from '../Button';
import { Secuence } from '../Secuence';
import { getPathLength } from '../../tools/general';

class Component extends React.Component {
  static displayName = 'CertificatePopup';

  static propTypes = {
    theme: PropTypes.object.isRequired,
    classes: PropTypes.object.isRequired,
    energy: PropTypes.object.isRequired,
    audio: PropTypes.object.isRequired,
    sounds: PropTypes.object.isRequired,
    className: PropTypes.any,
    certificate: PropTypes.shape({
      title: PropTypes.string,
      image: PropTypes.string,
      description: PropTypes.string
    }),
    onClose: PropTypes.func
  };

  componentDidMount () {
    document.addEventListener('keydown', this.handleKeyDown);
    this.enter();
  }

  componentWillUnmount () {
    document.removeEventListener('keydown', this.handleKeyDown);
  }

  enter () {
    const { energy } = this.props;
    const paths = this.svgElement.querySelectorAll('path');

    anime.set(paths, {
      strokeDasharray: getPathLength
    });

    anime({
      targets: paths,
      strokeDashoffset: [getPathLength, 0],
      easing: 'linear',
      duration: energy.duration.enter
    });

    anime({
      targets: this.element,
      opacity: [0, 1],
      scale: [0.9, 1],
      duration: energy.duration.enter,
      easing: 'easeOutCubic'
    });
  }

  exit () {
    const { energy } = this.props;
    const paths = this.svgElement.querySelectorAll('path');

    anime({
      targets: paths,
      strokeDashoffset: [0, getPathLength],
      easing: 'linear',
      duration: energy.duration.exit
    });

    anime({
      targets: this.element,
      opacity: [1, 0],
      scale: [1, 0.9],
      duration: energy.duration.exit,
      easing: 'easeOutCubic'
    });
  }

  handleKeyDown = event => {
    if (event.key === 'Escape') {
      this.props.onClose();
    }
  };

  handleBackgroundClick = event => {
    if (event.target === event.currentTarget) {
      this.props.onClose();
    }
  };

  render () {
    const {
      theme,
      classes,
      energy,
      audio,
      sounds,
      className,
      certificate,
      onClose,
      ...etc
    } = this.props;

    return (
      <div 
        className={cx(classes.root, className)} 
        onClick={this.handleBackgroundClick}
        {...etc}
      >
        <div 
          className={classes.popup}
          ref={ref => (this.element = ref)}
        >
          <div className={classes.frame}>
            <svg
              className={classes.svg}
              ref={ref => (this.svgElement = ref)}
              viewBox='0 0 100 100'
              preserveAspectRatio='none'
              xmlns='http://www.w3.org/2000/svg'
            >
              <path className={classes.path} d='M0,10 L0,0 L10,0' />
              <path className={classes.path} d='M90,0 L100,0 L100,10' />
              <path className={classes.path} d='M10,100 L0,100 L0,90' />
              <path className={classes.path} d='M100,90 L100,100 L90,100' />
            </svg>
          </div>
          <div className={classes.content}>
            <Secuence>
              <div className={classes.header}>
                <Text audio={audio} animation={{ animate: energy.animate }}>
                  {certificate.title}
                </Text>
              </div>
              <div className={classes.body}>
                <img 
                  src={certificate.image} 
                  alt={certificate.title}
                  className={classes.image}
                />
              </div>
              <div className={classes.footer}>
                <Button
                  className={classes.button}
                  audio={audio}
                  animation={{ animate: energy.animate }}
                  onClick={onClose}
                >
                  Close
                </Button>
              </div>
            </Secuence>
          </div>
        </div>
      </div>
    );
  }
}

export { Component }; 