import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '../tools/withStyles';
import { Main } from '../components/Main';
import { Text } from '../components/Text';
import { Fader } from '../components/Fader';
import { Secuence } from '../components/Secuence';
import { CertificatePopup } from '../components/CertificatePopup';

class Certifications extends React.Component {
  static propTypes = {
    classes: PropTypes.object
  };

  state = {
    selectedCertificate: null
  };

  certificates = [
    {
      id: 'oscp',
      title: 'OFFENSIVE SECURITY CERTIFIED PROFESSIONAL (OSCP)',
      issuer: 'OFFSEC (FEB 2024)',
      image: '/images/oscp_cert.jpeg',
      logo: '/images/OSCP_Logo.png',
      details: {
        intro: 'Advanced penetration testing certification covering:',
        points: [
          'Network Penetration Testing',
          'Web Application Security',
          'Buffer Overflow Exploitation',
          'Privilege Escalation',
          'Active Directory Attacks',
          'Client-Side Attacks',
          'Report Writing'
        ],
        description: `Successfully completed the OSCP certification with a focus on hands-on penetration testing skills. 
          The certification involved exploiting multiple machines in a lab environment and passing a 24-hour practical exam.
          Key achievements include mastering various exploitation techniques, developing custom scripts, and documenting findings.`
      }
    },
    {
      id: 'crtp',
      title: 'CERTIFIED RED TEAM PROFESSIONAL (CRTP)',
      issuer: 'PENTESTER ACADEMY (MAY 2022)',
      image: '/images/CRTP_cert.png',
      logo: '/images/crtp_logo.jpeg',
      details: {
        intro: 'Advanced Active Directory security certification covering:',
        points: [
          'Active Directory Penetration Testing',
          'Domain Privilege Escalation',
          'Lateral Movement Techniques',
          'Defense Evasion'
        ]
      }
    },
    {
      id: 'ecppt',
      title: 'ELEARNSECURITY CERTIFIED PROFESSIONAL PENETRATION TESTER (eCPPTv2)',
      issuer: 'ELEARNSECURITY (NOV 2021)',
      image: '/images/ecppt_cert.png',
      logo: '/images/ecpptv2_logo.jpeg',
      details: {
        intro: 'Professional-level penetration testing certification including:',
        points: [
          'Advanced Exploitation Techniques',
          'Post-Exploitation & Pivoting',
          'Web Application Security',
          'Report Writing'
        ]
      }
    },
    {
      id: 'ewpt',
      title: 'ELEARNSECURITY WEB APPLICATION PENETRATION TESTER (eWPT)',
      issuer: 'ELEARNSECURITY (DEC 2021)',
      image: '/images/ewpt_cert.png',
      logo: '/images/ewptv1_logo.jpeg',
      details: {
        intro: 'Specialized web application security certification covering:',
        points: [
          'Web Application Vulnerabilities',
          'Authentication & Authorization',
          'Client-Side Attacks',
          'Advanced Exploitation'
        ]
      }
    },
    {
      id: 'pnpt',
      title: 'PRACTICAL NETWORK PENETRATION TESTER (PNPT)',
      issuer: 'TCM SECURITY (JUL 2021)',
      image: '/images/pnpt_cert.png',
      logo: '/images/pnpt_logo.png',
      details: {
        intro: 'Hands-on network penetration testing certification (Retired in 2022):',
        points: [
          'External & Internal Network Penetration Testing',
          'Active Directory Exploitation',
          'Advanced Report Writing',
          'OSINT & Information Gathering',
          'Professional Report Preparation'
        ],
        description: 'Note: This certification was retired in 2022 but represents practical experience in network penetration testing and reporting.'
      }
    },
    {
      id: 'ejpt',
      title: 'ELEARNSECURITY JUNIOR PENETRATION TESTER (eJPT)',
      issuer: 'ELEARNSECURITY (APR 2021)',
      image: '/images/ejpt_cert.png',
      logo: '/images/ejptv1_logo.jpeg',
      details: {
        intro: 'Entry-level penetration testing certification covering:',
        points: [
          'Network Penetration Testing Basics',
          'Information Gathering Techniques',
          'Web Application Testing Fundamentals',
          'Basic Exploitation Methods',
          'Traffic Analysis'
        ]
      }
    }
  ];

  onCertificateClick = certificate => {
    this.setState({ selectedCertificate: certificate });
  };

  onClosePopup = () => {
    this.setState({ selectedCertificate: null });
  };

  componentDidMount() {
    if (window.location.hash) {
      const id = window.location.hash.substring(1);
      const element = document.getElementById(id);
      if (element) {
        setTimeout(() => {
          element.scrollIntoView({ behavior: 'smooth', block: 'center' });
          element.classList.add(this.props.classes.highlighted);
          setTimeout(() => {
            element.classList.remove(this.props.classes.highlighted);
          }, 2000);
        }, 500);
      }
    }
  }

  render () {
    const { classes } = this.props;
    const { selectedCertificate } = this.state;

    return (
      <Main className={classes.root}>
        <Secuence stagger>
          <article>
            <header>
              <h1><Text>Professional Certifications</Text></h1>
            </header>

            {this.certificates.map(cert => (
              <div 
                key={cert.id} 
                className={classes.certification}
                id={`cert-${cert.id}`}
              >
                <Fader>
                  <div 
                    className={classes.certContent}
                    onClick={() => this.onCertificateClick(cert)}
                  >
                    <div className={classes.certHeader}>
                      {cert.logo && (
                        <img 
                          src={cert.logo}
                          alt={`${cert.title} Logo`}
                          className={classes.certLogo}
                        />
                      )}
                      <div className={classes.certTitles}>
                        <h2><Text>{cert.title}</Text></h2>
                        <p><Text>{cert.issuer}</Text></p>
                      </div>
                    </div>
                    <div className={classes.details}>
                      <Text>{cert.details.intro}</Text>
                      <ul>
                        {cert.details.points.map((point, index) => (
                          <li key={index}><Text>{point}</Text></li>
                        ))}
                      </ul>
                    </div>
                  </div>
                </Fader>
              </div>
            ))}
          </article>
        </Secuence>

        {selectedCertificate && (
          <CertificatePopup
            certificate={selectedCertificate}
            onClose={this.onClosePopup}
          />
        )}
      </Main>
    );
  }
}

const styles = theme => ({
  root: {
    position: 'relative',
    padding: [20, 20, 30]
  },
  certification: {
    marginBottom: 40,
    '&:last-child': {
      marginBottom: 0
    }
  },
  certContent: {
    cursor: 'pointer',
    padding: 20,
    border: `1px solid ${theme.color.tertiary.main}`,
    backgroundColor: 'rgba(0, 0, 0, 0.3)',
    transition: `all ${theme.animation.time}ms ease-out`,
    borderRadius: 4,

    '&:hover': {
      borderColor: theme.color.secondary.main,
      backgroundColor: 'rgba(0, 0, 0, 0.5)',
      transform: 'translateY(-2px)'
    }
  },
  details: {
    marginTop: 20
  },
  highlighted: {
    animation: '$highlight 2s ease-out'
  },
  '@keyframes highlight': {
    '0%': {
      transform: 'scale(1)',
      borderColor: theme.color.secondary.main
    },
    '50%': {
      transform: 'scale(1.02)',
      borderColor: theme.color.tertiary.main
    },
    '100%': {
      transform: 'scale(1)',
      borderColor: theme.color.tertiary.main
    }
  },
  certHeader: {
    display: 'flex',
    alignItems: 'center',
    gap: 20,
    marginBottom: 15
  },
  certLogo: {
    width: 60,
    height: 60,
    objectFit: 'contain',
    borderRadius: 4,
    padding: 5,
    backgroundColor: 'rgba(0, 0, 0, 0.2)',
    border: `1px solid ${theme.color.tertiary.main}`,
    transition: `all ${theme.animation.time}ms ease-out`,
    '&:hover': {
      transform: 'scale(1.05)',
      borderColor: theme.color.secondary.main,
      backgroundColor: 'rgba(0, 0, 0, 0.4)'
    }
  },
  certTitles: {
    flex: 1,
    '& h2': {
      margin: 0,
      marginBottom: 5
    },
    '& p': {
      margin: 0,
      opacity: 0.8
    }
  },
  '@media (max-width: 480px)': {
    root: {
      padding: [10, 10, 20]
    },
    certContent: {
      padding: 15
    },
    certHeader: {
      flexDirection: 'column',
      alignItems: 'flex-start',
      gap: 10
    },
    certLogo: {
      width: 50,
      height: 50
    }
  },
  '@media (min-width: 481px) and (max-width: 768px)': {
    root: {
      padding: [15, 15, 25]
    }
  },
  '@media (min-width: 769px) and (max-width: 1024px)': {
    root: {
      padding: [20, 20, 30]
    }
  }
});

export default withStyles(styles)(Certifications);
